.page__body {
  max-width: @desktop-width;
  min-width: @mobile-width;
  box-sizing: border-box;
  margin: 0 auto;
  padding: 0;
  background-color: @white;
  font-family: "Open Sans", "Arial", "Helvetica", sans-serif;
}

.button {
  font-family: "Open Sans", "Arial", "Helvetica", sans-serif;
}

.page__body input[type="tel"],
.page__body input[type="text"],
.page__body input[type="email"],
textarea {
  font-family: "Open Sans", "Arial", "Helvetica", sans-serif;
}

.page-body__br-mobile-only {
  display: none;

  @media (max-width: @mobile-width-only) {
    display: inline;
  }
}

.page-body__br-tablet-only {
  display: none;

  @media (width: @tablet-width-only) {
    display: inline;
  }
}

.page-body__br-desktop-only {
  display: none;

  @media (min-width: @desktop-width) {
    display: inline;
  }
}

.only-in-desktop {
  display: none;

  @media (min-width: @desktop-width) {
    display: inline;
  }
}

.page-body__br-mobile-and-desktop {
  display: none;

  @media (width: @mobile-width-only) {
    display: inline;
  }

  @media (min-width: @desktop-width) {
    display: inline;
  }
}
