.main-form {
  padding-top: 44px;
  padding-bottom: 26px;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;

  @media (min-width: @tablet-width) {
    padding-top: 91px;
    padding-bottom: 55px;
    padding-left: 20px;
    padding-right: 20px;
  }

  @media (min-width: @desktop-width) {
    flex-direction: row;
    padding-left: 130px;
    padding-right: 130px;
    padding-bottom: 10px;
  }
}

.main-form__title {
  padding: 0;
  margin: 0;
  padding-left: 20px;
  padding-right: 20px;
  margin-bottom: 36px;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 30px;
  text-align: center;

  @media (min-width: @tablet-width) {
    font-size: 18px;
    padding-left: 65px;
    padding-right: 65px;
    margin-bottom: 72px;
  }

  @media (min-width: @desktop-width) {
    padding-left: 92px;
    padding-right: 85px;
    margin-bottom: 88px;
  }
}

.main-form__title-last {
  display: none;

  @media (min-width: @tablet-width) {
    display: inline;
  }
}

.main-form__fieldset {
  display: flex;
  flex-direction: column;
  padding: 0;
  margin: 0;
  border: none;
  color: @blue-basic;

  @media (min-width: @tablet-width) {
    border: 2px solid @grey-light;
    background-color: @white;
  }

  @media (min-width: @desktop-width) {
    width: 100%;
  }
}

.main-form__header {
  padding: 0;
  margin: 0;
  display: block;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 30px;
  text-align: left;
  color: @pink;
  text-transform: uppercase;
  margin-bottom: 18px;

  &--achievements {
    padding: 0;
    left: 0;
    right: 0;
    position: absolute;
    text-align: center;
    line-height: 18px;
  }

  &--application-type {
    width: 178px;
    padding: 0;
    margin: 0 auto;
    left: 0;
    right: 0;
    top: 38px;
    position: absolute;
    line-height: 18px;
    text-align: center;

    @media (min-width: @tablet-width) {
      margin-left: 67px;
    }

    @media (min-width: @desktop-width) {
      margin-left: 0;
    }
  }

  &--contacts {
    display: none;

    @media (min-width: @tablet-width) {
      display: inline-block;
      text-align: center;
    }
  }

  &--review {
    text-align: center;
    margin-bottom: 8px;
  }
}

.main-form__header-user-info {
  margin-left: 23px;
  margin-bottom: 6px;
}

.main-form__e-mail {
  margin-bottom: 14px;
}

.main-form__input {
  min-height: 61px;
  width: 100%;
  box-sizing: border-box;
  border: 2px solid @grey-light;
  border-radius: 2px;
  padding-left: 20px;
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: 30px;
  text-align: left;
  color: @blue-basic;

  &:hover {
    border-color: @blue-basic;
  }

  &:active {
    outline: none;
    border-color: @pink;
  }

  &::placeholder {
    font-size: 18px;
    font-style: normal;
    font-weight: 400;
    line-height: 30px;
    text-align: left;
    color: @blue-basic;
  }
}

.main-form__legend {
  @media (min-width: @tablet-width) {
    padding: 0;
    position: static;
    display: block;
    width: auto;
    padding-left: 30px;
    padding-right: 30px;
  }
}

.main-form__header--app-advanced {
  @media (min-width: @desktop-width) {
    padding-left: 4px;
    padding-right: 10px;
  }
}

.main-form__user-info {
  padding-bottom: 45px;
  box-sizing: border-box;

  @media (max-width: @mobile-width-only) {
    padding-left: 20px;
    padding-right: 20px;
  }

  @media (min-width: @tablet-width) {
    display: grid;
    grid-template-columns: min-content auto;
    align-items: baseline;
    row-gap: 54px;
    column-gap: 20px;
    padding-bottom: 60px;
    order: 2;
    border: none;
  }

  @media (min-width: @desktop-width) {
    width: auto;
    min-width: 433px;
  }
}

.main-form__user-info .main-form__header {
  @media (min-width: @tablet-width) {
    margin: 0;
  }
}

.main-form__user-info .main-form__input {
  @media (min-width: @tablet-width) {
    margin: 0;
  }
}

.main-form__input--surname {
  margin-bottom: 18px;
}

.main-form__input--name {
  margin-bottom: 18px;
  width: auto;
}

.main-form__user-info .main-form__input--name {
  @media (min-width: @tablet-width) {
    margin-left: -40px;
  }
}

.main-form__user-achievements {
  position: relative;
  padding-top: 37px;
  padding-left: 22px;
  padding-right: 22px;
  padding-bottom: 47px;
  margin: 0;
  background-color: @grey;

  @media (min-width: @tablet-width) {
    order: 5;
    background-color: @white;
    margin-bottom: 53px;
    padding-top: 29px;
    padding-bottom: 54px;
  }

  @media (min-width: @tablet-width) {
    padding-bottom: 6px;
    padding-right: 0;
    padding-left: 0;
  }
}

.main-form__label {
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: 30px;
}

.main-form__checkbox-list {
  margin: 0;
  padding: 0;
  padding-top: 78px;
  padding-left: 57px;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  list-style-type: none;

  @media (min-width: @tablet-width) {
    padding-top: 0;
    padding-left: 117px;
    padding-bottom: 48px;
  }

  @media (min-width: @desktop-width) {
    padding-bottom: 0;
    padding-left: 115px;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    column-gap: 90px;
  }
}

.main-form__checkbox-item {
  position: relative;
  margin-bottom: 52px;

  @media (min-width: @desktop-width) {
    margin-left: 4px;
  }

  &:last-child {
    margin-bottom: 0;
  }

  &--see-chuck-norris {
    @media (min-width: @desktop-width) {
      order: 2;
      margin-left: 12px;
    }
  }

  &--held-the-tower {
    @media (min-width: @desktop-width) {
      order: 3;
    }
  }

  &--burned-on-the-beach {
    @media (min-width: @desktop-width) {
      order: 4;
    }
  }

  &--buy-souvenirs {
    @media (min-width: @desktop-width) {
      order: 5;
      margin-left: 12px;
    }
  }

  &--crush-the-hotel {
    @media (min-width: @desktop-width) {
      order: 5;
    }
  }
}

.main-form__checkbox {
  display: none;
}

.main-form__checkbox + .main-form__label {
  &::after {
    display: block;
    position: absolute;
    content: "";
    left: -59px;
    top: -4px;
    width: 35px;
    height: 35px;
    border: 2px solid @grey-light;
    border-radius: 5px;
    background-color: @white;
    z-index: 7;
  }
}

.main-form__checkbox:checked + .main-form__label {
  &::before {
    display: block;
    position: absolute;
    content: "";
    left: -50px;
    top: 6px;
    z-index: 9;
    border: 2px solid @pink;
    border-radius: 2px;
    border-top: none;
    border-right: none;
    width: 21px;
    height: 9px;
    transform: rotate(-45deg);
  }
}

.main-form__checkbox + .main-form__label:hover {
  &::after {
    border-color: @blue-basic;
  }
}

.main-form__checkbox + .main-form__label:active {
  &::after {
    outline: none;
    border-color: @pink;
  }
}

