.main-form__header-tel {
  order: 2;
  position: relative;
  margin-left: 24px;
  margin-bottom: 6px;

  @media (min-width: @tablet-width) {
    margin-left: 20px;
    margin-bottom: 31px;
  }

  @media (min-width: @desktop-width) {
    margin-bottom: 0;
  }
}

.main-form__header-e-mail {
  order: 2;
  margin-left: 24px;
  margin-bottom: 6px;

  @media (min-width: @tablet-width) {
    margin-top: 10px;
    margin-left: 20px;
  }
}

.main-form__tel {
  position: relative;
  margin-bottom: 18px;

  @media (min-width: @tablet-width) {
    margin-bottom: 10px;
  }
}

.main-form__tel-address {
  padding-top: 32px;
  padding-bottom: 44px;
  padding-left: 20px;
  padding-right: 20px;

  @media (min-width: @tablet-width) {
    order: 4;
    padding-top: 11px;
    margin-bottom: 57px;
    padding-left: 57px;
    padding-right: 60px;
    padding-bottom: 23px;
  }

  @media (min-width: @desktop-width) {
    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: 74px;
    margin-top: -7px;
    padding-bottom: 10px;
  }
}

.main-form__application-type {
  position: relative;
  padding-top: 42px;
  padding-bottom: 45px;
  padding-left: 22px;
  padding-right: 22px;
  background-color: @grey;
  box-sizing: border-box;

  @media (min-width: @tablet-width) {
    order: 3;
    padding-left: 52px;
    padding-top: 49px;
    padding-bottom: 55px;
    margin-bottom: 55px;
    background-color: @white;
  }

  @media (min-width: @desktop-width) {
    width: 432px;
    box-sizing: border-box;
    padding-bottom: 0;
    margin-left: 72px;
    margin-top: -17px;
  }
}

.main-form__radio-list {
  box-sizing: border-box;
  list-style-type: none;
  padding: 0;
  margin: 0;
  padding-top: 70px;
  padding-left: 67px;

  @media (min-width: @tablet-width) {
    padding-top: 0;
  }

  @media (min-width: @desktop-width) {
    padding-left: 80px;
  }
}

.main-form__radio + .main-form__label--radio {
  &::after {
    display: block;
    position: absolute;
    content: "";
    left: -68px;
    top: -3px;
    width: 35px;
    height: 35px;
    border: 2px solid @grey-light;
    border-radius: 50%;
    background-color: @white;

    @media (min-width: @tablet-width) {
      left: -60px;
      top: -5px;
    }
  }
}

.main-form__radio + .main-form__label--radio:hover {
  &::after {
    border-color: @blue-basic;
  }
}

.main-form__radio + .main-form__label--radio:active {
  &::after {
    border-color: @pink;
  }
}

.main-form__radio:checked + .main-form__label--radio {
  &::before {
    width: 15px;
    height: 15px;
    position: absolute;
    content: "";
    top: 9px;
    left: -56px;
    background-color: @pink;
    border-radius: 50%;
    z-index: 9;

    @media (min-width: @tablet-width) {
      left: -48px;
      top: 7px;
    }
  }
}


.main-form__radio-item {
  position: relative;
  margin-bottom: 46px;

  &:last-child {
    margin-bottom: 0;
  }
}

.main-form__review {
  margin-top: 32px;
  margin-bottom: 52px;

  @media (max-width: @mobile-width-only) {
    padding-left: 20px;
    padding-right: 20px;
  }

  @media (min-width: @tablet-width) {
    order: 6;
    padding-left: 58px;
    padding-right: 60px;
    padding-top: 22px;
    padding-bottom: 52px;
    box-sizing: border-box;
    margin-top: 0;
    margin-bottom: 66px;
  }

  @media (min-width: @desktop-width) {
    padding-bottom: 42px;
  }
}

.main-form__review-header {
  padding: 0;
  margin: 0;
  margin-bottom: 22px;
  text-align: center;
}

.main-form__text-area {
  width: 100%;
  box-sizing: border-box;
  border: 2px solid @grey-light;
  border-radius: 2px;
  resize: none;
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: 30px;
  text-align: left;
  color: @blue-basic;
  min-height: 201px;
  padding-left: 20px;
  padding-right: 20px;
  padding-top: 14px;

  @media (min-width: @tablet-width) {
    min-height: 192px;
  }

  @media (min-width: @desktop-width) {
    min-height: 125px;
    padding-top: 10px;
    padding-bottom: 10px;
  }
}

.main-form__button-block {
  display: flex;
  flex-direction: column;

  @media (max-width: @mobile-width-only) {
    padding-left: 20px;
    padding-right: 20px;
  }

  @media (min-width: @tablet-width) {
    order: 7;
    flex-direction: row;
    justify-content: space-between;
    width: 100%;
    align-items: center;
  }
}

.main-form__button {
  background-color: @green;
  color: @white;
  text-transform: uppercase;
  min-height: 59px;
  font-size: 18px;
  font-style: normal;
  font-weight: 700;
  line-height: 30px;
  text-align: center;
  border-radius: 2px;
}

.main-form__button-submit {
  margin-bottom: 36px;
  min-height: 61px;

  &:hover {
    background-color: @green-dark;
  }

  &:active {
    background-color: @green-dark;
    color: fade(@white, 30%);
  }

  &:disabled {
    background-color: @grey-light;
  }

  @media (min-width: @tablet-width) {
    width: 298px;
  }

  @media (min-width: @tablet-width) {
    margin: 0;
  }
}

.main-form__footnote {
  margin: 0;
  padding: 0;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 30px;
  text-align: center;
  text-transform: uppercase;

  @media (min-width: @tablet-width) {
    margin-left: 130px;
  }

  @media (min-width: @desktop-width) {
    margin-right: 3px;
  }

  &::first-letter {
    color: @pink;
  }
}
