  height: 69px;
  z-index: -1;
}
@media (min-width: 660px) {
  .main-nav__item:first-of-type {
    height: 110px;
  }
}
@media (min-width: 1200px) {
  .main-nav__item:first-of-type {
    display: none;
  }
}
@media (min-width: 1200px) {
  .main-nav__item--current a {
    padding-bottom: 7px;
    border-bottom: 2px solid #FFFFFF;
  }
}
.main-nav__item-link {
  text-decoration: none;
  text-align: center;
  color: #FFFFFF;
}
.main-nav__item-link:hover {
  color: #D22856;
}
.main-nav__item-link:active {
  color: rgba(255, 255, 255, 0.3);
}
.main-nav__toggle {
  position: absolute;
  z-index: 10;
  top: 31px;
  right: 20px;
  width: 50px;
  height: 4px;
  border: none;
  border-radius: 1px;
  background-color: #FFFFFF;
}
.main-nav__toggle:hover {
  background-color: #D22856;
}
.main-nav__toggle:active {
  background-color: rgba(210, 40, 86, 0.3);
}
@media (min-width: 660px) {
  .main-nav__toggle {
    top: 55px;
  }
}
@media (min-width: 1200px) {
  .main-nav__toggle {
    display: none;
  }
}
.main-nav--opened .main-nav__toggle {
  transform: rotate(45deg);
  width: 33px;
}
.main-nav--opened .main-nav__toggle:hover {
  background-color: #D22856;
}
.main-nav--opened .main-nav__toggle:active {
  background-color: rgba(255, 255, 255, 0.3);
}
.main-nav--opened .main-nav__toggle::after {
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  width: 33px;
  height: 4px;
  border: none;
  border-radius: 1px;
  background-color: #FFFFFF;
  transform: rotate(90deg);
}
.main-nav--opened .main-nav__toggle:hover::after {
  background-color: #D22856;
}
.main-nav--opened .main-nav__toggle:active::after {
  background-color: rgba(255, 255, 255, 0.3);
}
.main-nav--closed .main-nav__toggle {
  box-shadow: 0 10px #FFFFFF, 0 -10px #FFFFFF;
}
.main-nav--closed .main-nav__toggle:hover {
  box-shadow: 0 10px #D22856, 0 -10px #D22856;
}
.main-nav--closed .main-nav__toggle:active {
  box-shadow: 0 10px rgba(210, 40, 86, 0.3), 0 -10px rgba(210, 40, 86, 0.3);
}
